import MetaTags from "common/MetaTags";
import { Page } from "common/components";
import { useTranslation } from "react-i18next";
import { getDescription } from "../descriptions";
import AntalyaAr from "./Antalya-ar";
import AntalyaEn from "./Antalya-en";
import IstanbulAr from "./Istanbul-ar";
import IstanbulEn from "./Istanbul-en";

const Cities = ({ contentId, titleId }) => {
  const [t, i18n] = useTranslation();

  const getTitle = () => t(titleId);

  const metaTags = [
    {
      name: "description",
      content: getDescription(contentId, i18n.language),
    },
  ];

  const renderSwitch = () => {
    switch (contentId) {
      case "istanbul":
        return i18n.language === "ar" ? (
          <IstanbulAr title={getTitle()} />
        ) : (
          <IstanbulEn title={getTitle()} />
        );
      case "antalya":
        return i18n.language === "ar" ? (
          <AntalyaAr title={getTitle()} />
        ) : (
          <AntalyaEn title={getTitle()} />
        );
      default:
        return null;
    }
  };

  return (
    <Page title={getTitle()}>
      <MetaTags title={getTitle()} tags={metaTags} />
      <div className="content">
        <div className="container">
          <div className="row">
            <div className="col-12">{renderSwitch()}</div>
          </div>
        </div>
      </div>
    </Page>
  );
};

export default Cities;
