import { istanbul } from "../img";

const Istanbul = ({ title }) => {
  return (
    <div className="terms-content">
      <div className="terms-text">
        <h1>{title}</h1>
        <p>اسطنبول: قارّتان ومدينة واحدة غارقة في تاريخ ثريٍّ مميّز.</p>
        <p>
          يعتبرها الكثيرون قلب وروح تركيا ، فهي تأسر الزوار بتنوعها اللامتناهي
          وعدد ما يمكنُ القيام به عند زيارتها. تستضيف اسطنبول عددًا لا يحصى من
          المتاحف والمساجد الكبرى ودور العبادة والقصور والأسواق والمناظر
          الخلابة.
        </p>
        <p>
          تتمتع اسطنبول بمناخ البحرالأبيض المتوسّط الرطب وكذلك بالمناخ المحيطي؛
          فهي تشهد فصول شتاء باردة مع هطول الأمطار بشكل متكرروصيفها دافئ إلى حار
          ومعتدل. عادة ما تكون فصول الربيع والخريف معتدلة، مع اختلاف الظروف
          الجويّة باختلاف اتجاه الرياح.
        </p>

        <div>
          <img src={istanbul} alt="User" className="rounded img-fluid" />
        </div>

        <div className="table-responsive">
          <table className="table table-hover table-center my-5">
            <thead>
              <tr>
                <th>
                  <p>مساحة إسطتبول الكبرى</p>
                </th>
                <th>
                  <p>مساحة إسطنبول الحضرية</p>
                </th>
                <th>
                  <p>التعداد السكاني</p>
                </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>
                  <p>~ 5,343 كم²</p>
                </td>
                <td>
                  <p>~ 2,577 كم²</p>
                </td>
                <td>
                  <p>~ 15,841,000 شخص</p>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div className="terms-text">
        <h2>السياحة العلاجيّة</h2>
        <p>
          تُعرف اسطنبول بأنها عاصمة السياحة العلاجيّة في تركيا، حيث يوجد في
          المدينة عدد لا يحصى من المرافق المتطورة والمعدّة للسياحة العلاجية بما
          في ذلك الينابيع الحارة والمنتجعات الصحية والمرافق المخصصة للسياحة
          العلاجية لكبار السن وذوي الاحتياجات الخاصّة.&nbsp;
        </p>
        <p>
          توظف جميع المستشفيات الخاصة والعامة موظفين يتحدثون لغة أجنبية على
          دراية جيدة بالسياحة العلاجية.
        </p>
        <p>
          كما وتخدم البنية التحتيّة المميّزة بخدماتها وطرقها ومرافقها المرضى
          وكبار السنّ وذوي الاحتياجات الخاصّة.
        </p>
      </div>
      <div className="terms-text">
        <h2>السياحة</h2>
        <h4>المواقع الأثرية</h4>
        <p>
          اسطنبول هي بوتقة نابضة بالحياة التي تعبقُ بالتراث الطبيعي والثقافي؛
          حيث كانت عاصمة الامبراطورية الرومانية (330-395) ، والامبراطورية
          البيزنطية (395-1453) والامبراطورية العثمانية (1453-1923). ويمكن
          الاطّلاع على آثار هذه الثقافات في كلّ زوايا اسطنبول حيث المتاحف
          والأسواق الأثرية والحمامات والأبنية القديمة، حيث تحتوى المدينة على
          أكثر من 70 متحفٍ حكوميّ وخاص تتيح الفرصة لاستكشاف تاريخ المدينة الغني.
        </p>
      </div>
      <div className="terms-text">
        <h4>فن الطهو</h4>
        <p>
          المطبخ التركي التقليدي لديه الكثير ليقدّمه من فطور مميّز وأطباق اللحوم
          وأطباق الخضار والشاي التركي التقليدي. كما تتميّز اسطنبول بثقافتها
          الغذائية وأذواقها اللذيذة التي تعود إلى المطبخ العثماني. وبالطّبع
          تتوفّر المطاعم التي تقدّم الأطباق العصرية بشكلٍ مميّزفي جميع مناطق
          المدينة.
        </p>
        <p>بعض الأطباق</p>
        <p>
          بوريك (فطيرة مالحة)، فطيرة بيدا (خبز تركي أو بيتزا)، مانتي (زلابية/
          شيشبرك)، دونر (شاورما) ، دولما (المحشي)، اسكندر كباب.&nbsp;
        </p>
        <p>الحلويات:</p>
        <p>بقلاوة ، كنافة ، سوتلاش (أرز بالحليب).&nbsp;</p>
      </div>
      <div className="terms-text">
        <h4>التسوق</h4>
        <p>
          يلوح البازار الكبير في الأفق باعتباره وجهة التسوق الأكثر شهرة في
          اسطنبول مع 61 شارعًا مغطى حيث يمكنك العثور على السجاد المنسوج يدويًا
          والمجوهرات الفاخرة والتوابل والعديد من الهدايا التذكارية التركية.
        </p>
        <p>
          إلى جانب ذلك يوجد في اسطنبول وفرة من مراكز التسوق المعاصرة والبوتيكات
          المستقلة التي تتنافس على جذب انتباهك.
        </p>
      </div>
      <div className="terms-text">
        <h4>السياحة الحلال</h4>
        <p>
          تناسب اسطنبول الزوّار المسلمين حيث تحتوي على الفنادق وأماكن الإقامة
          والأطعمة الموافقة للشريعة الإسلاميّة.
        </p>
      </div>
      <div className="terms-text">
        <h4>فنون وثقافة</h4>
        <p>
          تستضيف اسطنبول باعتبارها أكثر مدن تركيا كثافة سكانية عددًا لا يحصى من
          المعارض الفنيّة والمسارح ومراكز الحرف اليدويّة إضافةً إلى كلّ ما يشبع
          تذوّق محبّي الفنون والثقافة.
        </p>
      </div>
      <div className="terms-text">
        <h4>الطبيعة والتخييم</h4>
        <p>
          تتمتع اسطنبول بجمال فريد من نوعه مع مضيق البوسفور، وهو مضيق بطول 31 كم
          يمتد عبر المدينة ويفصل بين آسيا وأوروبا، وكذلك مرمرة والبحر الأسود.
        </p>
        <p>
          اسطنبول موطنٌ للجمال الطبيعي حيث يمكنك التنزه في الغابة والسباحة في
          خليج هادئ على البحر الأسود. كما ويمكن الاستمتاع بالمعسكرات التي تقع
          ضمن المناظر الطبيعية الجميلة خاصّة في المناطق الشمالية والغربية ،
          يمكنك العثور على الكثير من المعسكرات الهادئة التي تتميّز بالهدوء
          والجوّ اللطيف حتى في فصل الصيف.
        </p>
        <p>
          وبالنسبة لعشّاق الرياضة فيمكنهم قضاء بعض الوقت في الرحلات أو ركوب
          الدراجات أو ركوب الخيل أو لعب التنس أو كرة القدم أو كرة السلة في كلّ
          مناطق اسطنبول.
        </p>
      </div>
      <div className="terms-text">
        <h2>وسائل النقل</h2>
        <p>
          يمكنك الوصول إلى اسطنبول بالطائرة أو القطار أو الحافلة أو القارب
          وقيادة سيارتك الخاصة أو حتى مقطورة التخييم (الكارافان).
        </p>
        <p>
          يوجد في اسطنبول مطاران دوليان بمثابة اتصالك بالمدينة: مطار اسطنبول في
          الجانب الأوروبي وصبيحة كوكجن في الجانب الآسيوي. يتصل كلا المطارين
          اتصالاً جيدًا بوسط اسطنبول عن طريق خطوط الحافلات العامة وسيارات
          الأجرة. من الممكن أيضًا ركوب حافلة مكوكية (شاتل باص) أو وسيلة نقل خاصة
          لتوصلك مباشرةً إلى وجهتك.
        </p>
        <p>
          اسطنبول هي موطن لثاني أقدم أنظمة مترو أنفاق في العالم وتستضيف شبكة نقل
          جماعي متطوّرة، بما في ذلك الحافلات والحافلات الصغيرة وخدمات المترو
          وحافلات النقل السريع المعروفة باسم المتروباص ، ويمكن للمسافرين أيضًا
          استخدام تطبيقات التاكسي للتنقل في جميع أنحاء المدينة. كما وتتوفر
          الدراجات الناريّة والإلكترونية والهوائية ببنية تحتية مميّزة.
        </p>
      </div>
    </div>
  );
};

export default Istanbul;
