import { antalya } from "../img";

const Antalya = ({ title }) => {
  return (
    <div className="terms-content">
      <div className="terms-text">
        <h1>{title}</h1>
        <p>
          Antalya is the fourth most-visited city in the world with its sun,
          sandy beaches, rich history, and unique shops on every street. The
          city’s airport is open to international air traffic and was among the
          top 50 busiest airports in the world by passenger numbers in 2021.
        </p>
        <p>
          Embraced by the Mediterranean Sea at one side and cuddled by the
          mountains at the other. Temperate Mediterranean climate: summers are
          hot, dry and mild, winters are rainy yet very sunny.
        </p>

        <div>
          <img src={antalya} alt="User" className="rounded img-fluid" />
        </div>

        <div className="table-responsive">
          <table className="table table-hover table-center my-5">
            <thead>
              <tr>
                <th>
                  <p>Metropolitan Area</p>
                </th>
                <th>
                  <p>Urban Area</p>
                </th>
                <th>
                  <p>Population</p>
                </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>
                  <p>
                    ~ 20,591 Km<sup>2</sup>
                  </p>
                </td>
                <td>
                  <p>
                    1,417 Km<sup>2</sup>
                  </p>
                  <br />
                  <p>640 Km Riviera</p>
                </td>
                <td>
                  <p>~ 2,620,000 persons</p>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div className="terms-text">
          <h2>Medical Tourism</h2>
          <p>Antalya encompasses a wide range of treatments that include:</p>
          <div className="container" style={{ color: "#666" }}>
            <div className="row">
              <div className="col-3">-General surgery</div>
              <div className="col-3">-Organ transplant</div>
              <div className="col-3">-Thermal wellness</div>
            </div>
            <div className="row">
              <div className="col-3">-Elderly treatments</div>
              <div className="col-3">-Disabled treatments</div>
              <div className="col-3">-Dentistry</div>
            </div>
          </div>
          <br />
          <p>
            The city has a high-quality medical infrastructure with top doctors
            and advanced&nbsp;health facilities. Antalya is a very attractive
            destination for receiving medical treatment especially during the
            low season given its natural beauty, historical and cultural
            richness, and accessibility.&nbsp;
          </p>
        </div>
        <div className="terms-text">
          <h2>Tourism</h2>
          <h4>Ancient Sites</h4>
          <p>
            Throughout the history, Antalya hosted Romans, Persians, Egyptians,
            Byzantines, Greek, and Ottomans&nbsp;for thousands of years
          </p>
        </div>
        <div className="terms-text">
          <h4>Gastronomy</h4>
          <p>
            Whether you're looking for fine dining or cheap fast food, you
            will certainly be satisfied with what Antalya has to offer. Vegan-
            and vegetarian-friendly dishes can be easily found.
          </p>
        </div>
        <div className="terms-text">
          <h4>Shopping</h4>
          <p>
            Antalya include modern shopping malls, outdoor flea and street
            markets, antique, souvenir and handicraft shops where you can have
            your pick of antique or modern local authentic products, jewelry,
            and carpets.
          </p>
        </div>
        <div className="terms-text">
          <h4>Halal Tourism</h4>
          <p>
            Antalya is a suitable place for Muslim travelers. Muslim-friendly
            accommodation and food are easy to find.
          </p>
        </div>
        <div className="terms-text">
          <h4>Arts &amp; Culture</h4>
          <p>
            Antalya&nbsp;state galleries and cultural centers are open to
            visitors and host painting and sculpture exhibitions, and other fine
            arts activities.
          </p>
        </div>
        <div className="terms-text">
          <h4>Nature &amp; Camping</h4>
          <p>
            For nature lovers and campers, Antalya is so suitable with its
            unique nature; small and hidden bays, gorgeous turquoise water and
            white sand at an altitude of 1,200 meters. Exploring Taurus
            Mountains and the peaceful lakes, you can find beautiful landscape
            and safe camping grounds.
          </p>
          <p>
            While in&nbsp;Antalya, you can visit caves and hot&nbsp;springs,
            which are known for healing many illnesses with their mineral rich
            waters.
          </p>
        </div>
        <div className="terms-text">
          <h2>Transportation</h2>
          While in Antalya, you can use buses, streetcars, small collective
          buses called Dolmuş, and use the taxi applications for traveling
          around the city. Scooters, e-bikes, and bicycles are available with a
          well-established infrastructure.
        </div>
      </div>
    </div>
  );
};

export default Antalya;
