import { antalya } from "../img";
const Antalya = ({ title }) => {
  return (
    <div className="terms-content">
      <div className="terms-text">
        <h1>{title}</h1>
        <p>
          أنطاليا هي رابع أكثر المدن زيارة في العالم بشمسها وشواطئها الرملية
          وتاريخها الغني ومتاجرها الفريدة في كل شارع. مطار المدينة مفتوح للحركة
          الجوية الدولية وقد تمّ تسجيله من بين أكثر 50 مطارًا ازدحامًا في العالم
          من حيث عدد الركاب في عام 2021.
        </p>
        <p>
          يحتضنُ البحر الأبيض المتوسط ​​أنطاليا من جهة وتحتضنها الجبال من جهة
          أخرى. مناخها مناخ البحر الأبيض المتوسط ​​المعتدل: الصيف حار وجاف
          ومعتدل والشتاء ممطر ولكنه مشمس للغاية.
        </p>
        <div>
          <img src={antalya} alt="User" className="rounded img-fluid" />
        </div>
        <div className="table-responsive">
          <table className="table table-hover table-center my-5">
            <thead>
              <tr>
                <th>
                  <p>مساحة أنطاليا الكبرى</p>
                </th>
                <th>
                  <p>مساحة أنطاليا الحضرية</p>
                </th>
                <th>
                  <p>التعداد السكاني</p>
                </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>
                  <p>~ 20,591 كم²</p>
                </td>
                <td>
                  <p>1,417 كم²</p>
                  <br />
                  <p>640 كم ممشاة شاطئ</p>
                </td>
                <td>
                  <p>~ 2,620,000 شخص</p>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div className="terms-text">
          <h2>السياحة العلاجيّة</h2>
          <p>
            تحتوي أنطاليا على مجموعة واسعة من العلاجات التي تشمل: الجراحة العامة
            وزراعة الأعضاء والينابيع الحرارية وعلاجات كبار السن وعلاجات ذوي
            الاحتياجات الخاصّة وعلاجات الأسنان.
          </p>
          <p>
            تحتوي أنطاليا على بنية تحتية طبية عالية الجودة مع أفضل الأطباء
            والمرافق الصحية المتقدمة. و المدينة وجهة جذابة للغاية لتلقي العلاج
            الطبي خاصة خلال موسم السياحة منخفض الكلفة نظرًا لجمالها الطبيعي
            وثرائها التاريخي والثقافي وإمكانية الوصول إليها.
          </p>
          <p>السياحة</p>
        </div>
        <div className="terms-text">
          <h4>المواقع الأثرية</h4>
          <p>
            استضافت أنطاليا على مر التاريخ الرومان والفرس والمصريين والبيزنطيين
            واليونانيين والعثمانيين لآلاف السنين.
          </p>
        </div>
        <div className="terms-text">
          <h4>فن الطهو</h4>
          <p>
            سواء كنت تبحث عن عشاء فاخر أو وجبات سريعة رخيصة ستكون راضيًا
            بالتأكيد عما تقدمه لك أنطاليا؛ حيث يمكن العثور بسهولة على الأطباق
            الحيوانيّة والنباتية.
          </p>
          <p>التسوق</p>
          <p>
            تتميّز أنطاليا باحتوائها على مراكز التسوق الحديثة والأسواق المفتوحة
            في الهواء الطلق وحتى السلع المستعملة. كما وتتميّز بمحلات التحف
            والهدايا التذكارية والحرف اليدوية حيث يمكنك الاستمتاع باختيار
            المجوهرات والسجّاد المحلّيّ العتيق أوالحديث.
          </p>
        </div>
        <div className="terms-text">
          <h4>السياحة الحلال</h4>
          <p>
            أنطاليا مكان مناسب للمسافرين المسلمين حيث تحتوي على الفنادق وأماكن
            الإقامة والأطعمة المناسبة للمسلمين.
          </p>
        </div>
        <div className="terms-text">
          <h4>فنون وثقافة</h4>
          <p>
            تحتوي أنطاليا على العديد من المعارض والمراكز الثقافية كما وتستضيف
            معارض الرسم والنحت وأنشطة الفنون الجميلة الأخرى.
          </p>
        </div>
        <div className="terms-text">
          <h4>الطبيعة والتخييم</h4>
          <p>
            أنطاليا مناسبة جدًا لمحبي الطبيعة والتخييم بطبيعتها الفريدة التي
            تضمّ الخلجان الصغيرة والمياه الفيروزية الرائعة والرمال البيضاء.
            يمكنك أن تجد في جبال طوروس والبحيرات الهادئة مناظر طبيعية جميلة
            وأماكن تخييم آمنة.
          </p>
          <p>
            كما ويمكنك أثناء تواجدك في أنطاليا زيارة الكهوف والينابيع الساخنة
            المعروفة بعلاج العديد من الأمراض بمياهها الغنية بالمعادن.
          </p>
        </div>
        <div className="terms-text">
          <h2>وسائل النقل</h2>
          <p>
            يمكنك في أنطاليا استخدام الحافلات وعربات الترام والحافلات الجماعية
            الصغيرة المسماة Dolmuş واستخدام تطبيقات التاكسي للتنقل في جميع أنحاء
            المدينة. كما وتتوفر الدراجات الناريّة والإلكترونية والهوائية ببنية
            تحتية مميّزة.
          </p>
        </div>
      </div>
    </div>
  );
};

export default Antalya;
