import { istanbul } from "../img";

const Istanbul = ({ title }) => {
  return (
    <div className="terms-content">
      <div className="terms-text">
        <h1>{title}</h1>
        <p>
          Istanbul, two continents, one city steeped in rich, intriguing
          history.
        </p>
        <p>
          Many consider as the heart and soul of Türkiye, Istanbul captivates
          visitors with its endless variety of things to do. It plays host to
          countless grand mosques, museums, churches, palaces, bazaars, and
          scenic panoramas.
        </p>
        <p>
          Istanbul has a borderline Mediterranean humid and oceanic climate. It
          experiences cool winters with frequent precipitation. Its summers are
          warm to hot and moderate. Springs and falls are usually mild, with
          varying conditions dependent on wind direction.{" "}
        </p>

        <div>
          <img src={istanbul} alt="User" className="rounded img-fluid" />
        </div>

        <div className="table-responsive">
          <table className="table table-hover table-center my-5">
            <thead>
              <tr>
                <th>
                  <p>Metropolitan Area</p>
                </th>
                <th>
                  <p>Urban Area</p>
                </th>
                <th>
                  <p>Population</p>
                </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>
                  <p>
                    ~ 5,343 Km<sup>2</sup>
                  </p>
                </td>
                <td>
                  <p>
                    ~ 2,577 Km<sup>2</sup>
                  </p>
                </td>
                <td>
                  <p>~ 15,841,000 persons</p>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div className="terms-text">
        <h2>Medical Tourism</h2>
        <p>
          Istanbul is known as the health tourism capital of T&uuml;rkiye. The
          city has countless facilities for health tourism including thermal and
          spa wellness, and tourism for elderly and disabled people.
        </p>
        <p>
          Istanbul has a well-developed user- friendly infrastructure that
          provides all support to the ill people, elderlies, and persons with
          special needs including services and transportation.
        </p>
        <p>
          All private and public hospitals employ foreign language speaking
          personnel who are well informed about health tourism.
        </p>
      </div>
      <div className="terms-text">
        <h2>Tourism</h2>
        <h4>Ancient Sites</h4>
        <p>
          Istanbul is a vibrant melting pot of natural and cultural heritage; It
          was the capital of the Roman Empire (330-395), the Byzantine Empire
          (395-1453) and the Ottoman Empire (1453-1923). The effects of these
          cultures can be found in all corners of Istanbul, where museums,
          ancient markets, baths and ancient buildings are located. The city
          contains more than 70 government and private museums that provide an
          opportunity to explore the city's rich history.
        </p>
      </div>
      <div className="terms-text">
        <h4>Gastronomy</h4>
        <p>
          Traditional Turkish cuisine has so much to offer, from the doner meat
          and meat dishes to the vegetable dishes and traditional Turkish tea.
          Istanbul also draws attention with its food culture and delicious
          tastes, which date back to the Ottoman cuisine. Of course, there are
          restaurants that serve modern dishes in a distinctive way in all areas
          of the city.
        </p>
        <p>Just to name a few dishes:</p>
        <p>
          <a href="https://nomadparadise.com/borek-recipe/">
            Borek (Savory Pie)
          </a>
          , Pide (Turkish Flatbread or Pizza), Manti (Turkish Dumplings), Doner,
          Dolma, Alinazik, Iskender Kebab.
        </p>
        <p>Deserts:</p>
        <p>Baklava, Kunefe, and Sutlach (rice pudding).</p>
      </div>
      <div className="terms-text">
        <h4>Shopping</h4>
        <p>
          The Grand Bazaar looms large as Istanbul's most renowned
          shopping destination, with 61 covered streets where you can find
          hand-woven carpets, fine jewelry, spices, and many more Turkish
          souvenirs.
        </p>
        <p>
          Besides, Istanbul has abundance of contemporary shopping malls and
          independent boutiques vying for your attention.
        </p>
        <p>HalalTourism</p>
        <p>
          Muslim-friendly accommodation and food are easy to find in the vast
          city of Istanbul.
        </p>
      </div>
      <div className="terms-text">
        <h4>Arts &amp; Culture</h4>
        <p>
          As the most densely populated city in Turkey, Istanbul hosts countless
          art galleries, theaters, and handicraft centers, in addition to
          everything that satisfies the taste of arts and culture lovers.
        </p>
      </div>
      <div className="terms-text">
        <h4>Nature &amp; Camping</h4>
        <p>
          Istanbul has a unique beauty with the Bosporus, a 31 km long strait
          running across the city separating Asia and Europe, as well as Marmara
          and Black Sea.
        </p>
        <p>
          Home to natural beauty; you can stroll in the forest, have a swim in a
          quiet cove of the Black Sea, resort to campsites nestled in beautiful
          landscapes. Especially the north and western regions, you can find a
          lot of quiet campsites nestled in beautiful landscapes with quiet
          nature and cool air even in summer.
        </p>
        <p>
          Sports lovers will be satisfied spending some time trekking, cycling,
          or horse riding or playing tennis, football, basketball, or street
          basketball.
        </p>
      </div>
      <div className="terms-text">
        <h2>Transportation</h2>
        <p>
          You can get to Istanbul by plane, train, bus, boat and driving your
          own car or even caravan.
        </p>
        <p>
          Two international airports can be your connection to Istanbul,
          Istanbul Airport in the European side and Sabiha Gokcen in the Asian
          side. Both airports are well connected to Istanbul's center by
          public bus lines. It is also possible to take a shuttle bus, a private
          transfer or an executive transfer that will bring you directly to your
          destination.
        </p>
        <p>
          Istanbul is home to the world's second-oldest subway systems and hosts
          a complex mass transit network, including buses, minibuses, metro
          services, the bus rapid transit known as metrobus and travelers can
          also use the biTaxi application for traveling around the city.
          Scooters, e-bikes, and bicycles are available with a well-established
          infrastructure.
        </p>
      </div>
    </div>
  );
};

export default Istanbul;
